<script>
  import { onMount } from 'svelte';
  import Swiper from 'swiper';
  import 'swiper/swiper-bundle.css';
  import { navigate } from "svelte-routing"; // Re-import navigate function

  export let title;
  export let contentItems = [];

  let swiperContainer;

  onMount(() => {
    const swiper = new Swiper(swiperContainer, {
      // Optional parameters
      loop: true,
      slidesPerView: 3, // Changed back to 3
      spaceBetween: 30,
      autoHeight: true, // Added to allow container to adjust height

      // If we need pagination
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },

      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });
  });

  function goToContentDetail(id) {
    navigate(`/content/${id}`);
  }

  function handleKeydown(event, id) {
    if (event.key === 'Enter' || event.key === ' ') {
      goToContentDetail(id);
    }
  }
</script>

<div class="carousel-section">
  <h2>{title}</h2>
  <div class="swiper-container" bind:this={swiperContainer}>
    <div class="swiper-wrapper">
      {#each contentItems as item (item.id)}
        <div
          class="swiper-slide"
          on:click={() => goToContentDetail(item.id)}
          on:keydown={(e) => handleKeydown(e, item.id)}
          style="cursor: pointer;"
          tabindex="0"
          role="button"
        >
          <h3>{item.title}</h3>
          <p class="tags">{item.tags.join(' ')}</p>
          <!-- <p class="description">{item.description}</p> --> <!-- Removed description -->
        </div>
      {/each}
      {#if contentItems.length === 0}
        <div class="swiper-slide"> </div>
      {/if}
    </div>
    <!-- Add Pagination -->
    <div class="swiper-pagination"></div>

    <!-- Add Navigation -->
    <div class="swiper-button-prev"></div>
    <div class="swiper-button-next"></div>
  </div>
</div>

<style>
  .carousel-section {
    padding: 60px 20px; /* Increased vertical padding */
    background-color: #fff;
  }

  h2 {
    text-align: center;
    margin-bottom: 80px; /* Increased margin-bottom */
    font-size: 3rem; /* Increased font size */
    color: #333;
  }

  .swiper-container {
    width: 100%;
    /* height: 300px; */ /* Removed fixed height */
    overflow: hidden;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #eee;

    /* Center slide text vertically */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 15px; /* Added border-radius for rounded corners */
    padding: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Add a subtle shadow */
  }

  .swiper-slide h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
  }

  .swiper-slide .tags {
    font-size: 0.9rem;
    color: #666;
  }

  /* Removed unused .swiper-slide .description */
</style>